
#app {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
}
/*#app * {
  margin: 0;
  padding: 0;
}*/


body[data-v-3cbf3930] {
    height: 100vh;
    display: grid;
    /*place-content:center;*/
    background-color: #222222;
}
.square[data-v-3cbf3930] {
    background-color: white;
    display: grid;
    grid-template-columns: 22vmin 52vmin 22vmin;
    grid-template-rows: 22vmin 52vmin 22vmin;
    grid-gap: 1vmin;
    grid-template-areas:
        "g1 g2  g3"
        "g4 g5  g6"
        "g7 g8  g9"
}
.info[data-v-3cbf3930] {
    grid-area: g7;
    text-align: left;
    height: 100%;
    overflow: auto;
}
.board[data-v-3cbf3930] {
    grid-area: g5;
    display: grid;
    grid-template-columns: repeat(20, 1fr);
    grid-template-rows: repeat(20, 1fr);
    border: 0.1vmin solid currentColor;
}
.choose[data-v-3cbf3930] {
    grid-area: g3;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-gap: 1vmin;
    grid-template-areas:
        "cin cin cin cin"
        ". cp cp ."
        ". cp cp ."
        "btn1 btn1 btn2 btn2";
}
.scoreboard[data-v-3cbf3930] {
    grid-area: g9;
}
.player[data-v-3cbf3930] {
    display: grid;
    grid-gap: 0.5vmin;
}
.player1[data-v-3cbf3930] {
    grid-area: g2;
    grid-template-columns: repeat(7, 7vmin);
    grid-template-rows: repeat(3, 7vmin);
}
.player2[data-v-3cbf3930] {
    grid-area: g4;
    grid-template-columns: repeat(3, 7vmin);
    grid-template-rows: repeat(7, 7vmin);
}
.player3[data-v-3cbf3930] {
    grid-area: g8;
    grid-template-columns: repeat(7, 7vmin);
    grid-template-rows: repeat(3, 7vmin);
}
.player4[data-v-3cbf3930] {
    grid-area: g6;
    grid-template-columns: repeat(3, 7vmin);
    grid-template-rows: repeat(7, 7vmin);
}
.piece[data-v-3cbf3930] {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(5, 1fr);
    border: 0.1vmin solid currentColor;
}
.cel[data-v-3cbf3930] {
    place-content: center;
    border: 0.1vmin solid currentColor;
}
.cel.-hidden[data-v-3cbf3930] {
    display: none;
}
.red[data-v-3cbf3930] {
    background-color: #FF0000;
}
.yellow[data-v-3cbf3930] {
    background-color: #FFaa00;
}
.blue[data-v-3cbf3930] {
    background-color: #009999;
}
.green[data-v-3cbf3930] {
    background-color: #00cc00;
}

